
























































import { Component, Vue, Watch } from 'vue-property-decorator'
import Slider from '@/components/Slider.vue'
import { State, Mutation } from 'vuex-class'

@Component({
    components: {
        Slider
    }
})
export default class Lab extends Vue {
    @State public card: string
    public dialog: boolean = false
    public iTime: number = 4
    public timer: any = null

    public users: object[] = [
        {
            id: '569AF41F',
            name: '张少龙',
            class: '初一（4）班',
            sex: '男',
            no: '20180104032',
            lab: '实验室二',
            pic:  require('@/assets/images/user/user1.jpg')
        },
        {
            id: 'C3EDB424',
            name: '李大伟',
            class: '初二（5）班',
            sex: '男',
            no: '201700205023',
            lab: '实验室一',
            pic:  require('@/assets/images/user/user2.jpg')
        },
        {
            id: '4684701F',
            name: '王佩佩',
            class: '初一（1）班',
            sex: '女',
            no: '20180101012',
            lab: '实验室三',
            pic:  require('@/assets/images/user/user3.jpg')
        }
    ]

    public target: any = {}

    @Mutation public setCard: (id: any) => {}
    @Watch('card') public onCard(): void {
        if (this.card) {

            const target = this.users.find((user: any) => user.id === this.card)
            this.target = target ? target : this.users[0]

            this.dialog = true
            this.setCard(null)
            this.iTime = 4
            if (this.timer) {
                clearInterval(this.timer)
                this.timer = null
            }
            this.timer = setInterval(() => {
                this.iTime--
                if (this.iTime == 0) {
                    clearInterval(this.timer)
                    this.timer = null
                    this.dialog = false
                }

            }, 1000)

        } else {
            this.iTime = 4
        }
    }

}
