


















import { Component, Vue } from 'vue-property-decorator'
interface IMenu{
    name?: string,
    path?: string,
    icon?: string,
    children?: Array<IMenu>
}

@Component({
})
export default class Slider extends Vue {

    public currentMenu: IMenu = {}

    public menus: object[] = [
        {
            name: '主页',
            path: '',
            icon: 'home'
        },
        {
            name: '设备管理',
            path: 'device',
            icon: 'device',
            children: [
                {
                    name: '设备基本信息',
                    path: '/deviceinfo',
                    icon: '',
                } ,
                {
                    name: '设备使用记录',
                    path: '/devicerecord',
                    icon: '',
                }
            ]
        },
        {
            name: '耗材管理',
            path: '/material',
            icon: 'material'
        },
        {
            name: '人员管理',
            path: 'user',
            icon: 'user',
            children: [
                {
                    name: '学生管理',
                    path: '/studentmgr',
                    icon: '',
                } ,
                {
                    name: '教师管理',
                    path: '/techermgr',
                    icon: '',
                }
            ]
        },
        {
            name: '能源管理',
            path: '/energy',
            icon: 'energy'
        },
        {
            name: '实验室管理',
            path: '/laboratory',
            icon: 'laboratory'
        },
        // {
        //     name: '安防管理',
        //     path: '/security',
        //     icon: 'security'
        // }
    ]
    public active: string = '主页'

    public onMenuClick(menu: any): void {
        if (menu.name === this.currentMenu.name) {
            this.currentMenu = {}
            this.active = ''
            return
        }
        this.currentMenu = menu
        this.active = menu.name
        if (!menu.children) {
            this.$router.push(`/lab${this.currentMenu.path}`)
        }
    }

    public onSonMenuClick(smenu: any) {
        this.active = smenu.name
        this.$router.push({name: smenu.name})
    }

    public created(): void {
        const { name } = this.$route
        const menu: any  = this.menus.find((menu: any) => menu.name === name)
        if (menu) {
            this.active = menu.name
        }
    }
}
